@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap);
.video {
  width: 100vw;
  display: flex;
  margin-top: 25px;
  height: 100vh;
}
.video > div {
  width: 600px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
video {
  width: 100%;
}
@media screen and (max-width: 1100px) {
  .video > div {
    width: 400px;
  }
}
@media screen and (max-width: 900px) {
  .video {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
  }
}
@media screen and (max-width: 50px) {
  .video > div {
    width: 300px;
  }
}

.form {
  width: 100%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(158, 210, 255, 0.54);
  grid-gap: 1rem;
  gap: 1rem;
  position: relative;
}
.form > div {
  display: flex;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}
.form > div input {
  padding: 5px;
  outline: none;
  border: none;
  border-radius: 5px;
}
.button {
  color: white;
  background-color: #5a77e8;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
  text-decoration: none;
  font-weight: 400;
  margin: 5px 0;
}

.call {
  position: absolute;
  top: 0;
  right: 0;
  margin: 1rem 2rem;
  padding: 1rem;
  background-color: aliceblue;
  border-radius: 10px;
}
.call > div:nth-child(2) {
  margin-top: 10px;
  background-color: rgb(34, 189, 34);
  color: white;
  font-weight: 700;
  text-align: center;
}
.decline {
  background-color: rgb(215, 53, 53);
  color: white;
  text-align: center;
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  grid-gap: 20px;
  gap: 20px;
  background-color: rgba(240, 248, 255, 0.393);

}
.header a {
  color: white;
  background-color: #5a77e8;
  border-radius: 10px;
  padding: 5px 10px;
  cursor: pointer;
  text-decoration: none;
  font-weight: 500;
}

body {
  background: url(https://images.unsplash.com/photo-1618556450994-a6a128ef0d9d?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=764&q=80);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
body::-webkit-scrollbar {
  display: none;
}
* {
  margin: 0;
  padding: 0;
  font-family: "Rubik", sans-serif;
}

